  submitFormDataJson(_url, _params): Observable<any> {
    console.log(" submitFormData _url >>>>"+ _url);
    // console.log(" submitFormData _params >>>>"+ JSON.stringify(_params));
    let hdr = {
        'Content-Type': 'application/json'
    };
    let options = { headers: hdr };
    return this.http.post<any>(environment.hostUrl+'/'+_url, _params, options)
  }



  


