showGeneralModal(pageData:any){

  if(pageData.checkSession){
    this.checkSessionExpired(localStorage.getItem('userId'))
     .then((data:any)=>{
   if(data==0){
    this.hideGeneralModal();
    this.logout(localStorage.getItem('userId'));
    return  ;
   }
   else{



    const dialogConfig = new MatDialogConfig();

    dialogConfig.disableClose = true;
    dialogConfig.autoFocus = true;
    dialogConfig.panelClass='custom-modalbox-small';
    dialogConfig.data = {
  
        description:'description',
        modelData: pageData
    };
    this.dialogRef =  this.dialog.open(GeneralModalComponent,
  dialogConfig);

   }
   });
   }
   else{



    const dialogConfig = new MatDialogConfig();

    dialogConfig.disableClose = true;
    dialogConfig.autoFocus = true;
    dialogConfig.panelClass='custom-modalbox-small';
    dialogConfig.data = {
  
        description:'description',
        modelData: pageData
    };
    this.dialogRef =  this.dialog.open(GeneralModalComponent,
  dialogConfig);

   }


}

hideGeneralModal(){
if(this.dialogRef)
  this.dialogRef.close();
}

close() {
  this.dialogRef.close();
   
}

closeAndLogout() {
  this.dialogRef.close();
  this.dialogRef.afterClosed().subscribe((result) => {
    if (result === 'closed') {
       this.logout(localStorage.getItem('userId'));
    }
  });
}
closeAndRedirect(url) {
  this.dialogRef.close();
  this.dialogRef.afterClosed().subscribe((result) => {
    if (result === 'closed') {
         this.router.navigate([url]);
    }
  });
}

