import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import {MAT_DIALOG_DATA, MatDialogRef} from "@angular/material";
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { CommonService } from '../services/common-service.service';
import { LGIssuanceModel } from '../models/lgIssuance.model';
import { DataTablesModule } from 'angular-datatables';
import { ChartType, ChartOptions } from 'chart.js';
import { SingleDataSet, Label, monkeyPatchChartJsLegend, monkeyPatchChartJsTooltip } from 'ng2-charts';
import {TranslateService} from '@ngx-translate/core';
declare var require: any

@Component({
  selector: 'app-admin-dash-board',
  templateUrl: './admin-dash-board.component.html',
  styleUrls: ['./admin-dash-board.component.css']
})
export class AdminDashBoardComponent implements OnInit {

  userId: string;
  userName: string;
  roleId: string;
  isLGClicked:boolean = true;
  LGLists: Object;
  PendingLG: string;
  ExpiredLG: string;
  ActiveLG: string;
  PendingApprovalLG: string;
  picolor: Array<string> = ['#008077','#02282b','#0f7ff5','#02423d','#3bbcf3','#78f1b5','#0b8d4d','#02264c','#0064ca','#012b28'];
  i:number=0;
  dtOptions: DataTables.Settings = {};
  dtOptionsAR: DataTables.Settings = {};

  


  public pieChartOptions: ChartOptions = {
    responsive: true,
    legend: {position: 'bottom',
    labels: {
      fontSize: 8
    }
  }
    
  };


  public pieChartLabels: Label[] = [];
  public pieChartData: SingleDataSet = [];
  public pieChartType: ChartType = 'pie';
  public pieChartLegend = true;
  public pieChartPlugins = [];
  public doughnutposition:string= 'top';
  public pieChartColors = [
    {
     backgroundColor: []
    },
  ];






  constructor(private router: Router, private formBuilder: FormBuilder, private commonService: CommonService,public translate: TranslateService) { 
    monkeyPatchChartJsTooltip();
    monkeyPatchChartJsLegend();

  }

  ngOnInit() {
    this.userId = localStorage.getItem('userId');
    console.log("this.userId::::"+this.userId);
    this.userName = localStorage.getItem('userName');
    console.log("this.userName::::" + this.userName);

    this.roleId = localStorage.getItem('roleId');
    console.log("this.role_ID::::" + this.roleId);

/*************************************************** */

this.dtOptionsAR = {
  pageLength: 10,
  processing: true,
  language: {
    processing: " ...",
    search: ":",
    lengthMenu: " _MENU_ ",
    info: " _START_  _END_   _TOTAL_ ",
    infoEmpty: " 0  0   0 ",
    infoFiltered: "(   _MAX_ )",
    infoPostFix: "",
    loadingRecords: " ...",
    zeroRecords: "    ",
    emptyTable: "     ",
    paginate: {
      first: "",
      previous: "",
      next: "",
      last: ""
    },
    aria: {
      sortAscending: ":    ",
      sortDescending: ":    "
    }
  }
};

this.dtOptions = {
  pageLength: 10,
  processing: true,
};






this.commonService.checkSessionExpired(this.userId)
      .then((data:any)=>{

   if(data==0){
    this.commonService.showGeneralMessage(this.translate.instant('HOME.Session_Expired'),this.translate.instant('HOME.logged_out'),false);
    this.commonService.logout(this.userId);
     return  ;
   }
   else{


   }
  });
/************************************************** */

    this.commonService.adminGetGraphData()
    .subscribe(res => {
      console.log(res);
       for (var val of res.userObject) {
       this.pieChartLabels.push(val.lg_type);
       this.pieChartData.push(val.no_of_count);
       this.pieChartColors[0].backgroundColor.push(this.picolor[this.i]);
       this.i++;
     }
    });



   //To get pending list
   this.commonService.getActiveLgListForAdmin()
   .subscribe(res => {
    console.log(res);
     this.LGLists = res.userObject;

   });



   this.commonService.adminCountOfActive()
   .subscribe(res => {
     console.log(res);
     this.ActiveLG = res;

   });
   
   this.commonService.adminCountOfPending()
   .subscribe(res => {
     console.log(res);
     this.PendingLG = res;

   });

   this.commonService.adminCountOfExpired()
   .subscribe(res => {
     console.log(res);
     this.ExpiredLG = res;
     //console.log("CountOfNotPending::::" + JSON.stringify(this.ActiveLG));

   });

   this.commonService.adminCountOfPendingApproval()
   .subscribe(res => {
     console.log(res);
     this.PendingApprovalLG = res;
     //console.log("CountOfNotPending::::" + JSON.stringify(this.ActiveLG));

   });
   
   
  }
}
